import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/index',
      redirect: '/',
    },
    {
      path: '/',
      component: () => import('@/views/MainPage.vue'),
    },
  ],
});

export default router;
